		flex-direction: column;
		color: #FFFFF9;
		text-shadow: 1px 1px #FFFFFF;

	}

	.main-wrap {
		display: flex;
		width: 100%;
		flex-direction: column;
	}
	.main-wrap > *{
		display: flex;
		justify-content: center;
	}

	.main > h5 {
		text-decoration: none;
		color: #FFFFF9;
		justify-content: center;
		display: flex;
		margin-top: 30%;
	}
	.main > h5:hover {
		color: #40403F;
		cursor: pointer;
	}

}

